<template>
  <f7-page name="activityJoin" no-toolbar @page:beforein="onPageBeforeIn">
    <f7-navbar back-link="" title=""></f7-navbar>

    <f7-block class="text-align-left">
      <div>
        <i class="f7-icons icon">money_yen_round</i>
        <span>{{petUnitPrice}} {{personUnitPrice}}</span>
      </div>
    </f7-block>

    <f7-block class="text-align-left">
      <div>
        <p></p>
        <f7-stepper :input="false" round :min="1" :max="2" @stepper:change="onPersonChange"></f7-stepper>
      </div>

      <div>
        <p></p>
        <f7-stepper :input="false" round :min="1" :max="2" @stepper:change="onPetChange"></f7-stepper>
      </div>

      <div>
        <p>{{allPrice}} </p>
      </div>
    </f7-block>

    <f7-block v-if="status === 1">
      <f7-row>
        <f7-button class="col" raised @click="back"></f7-button>
        <f7-button class="col" fill raised color="green" @click="orderCreate"></f7-button>

        <!-- <f7-button class="col" fill raised color="green" @click="submitData"></f7-button> -->
      </f7-row>
    </f7-block>

    <f7-block v-if="status === 2" class="text-align-center"></f7-block>
  </f7-page>
</template>

<script>
import {
  getActivityDetail,
  joinActivity,
  getUserInfo,
  createOrder,
  unifiedOrder,
  getOrderInfoForActivityId
} from '../utils'

const userMap = {
  mobile: '',
  name: '',
  birth: '',
  gender: '',
  age: '',
  city: '',
  province: '',
  address: '',
  idCard: '',
  high: ''
}

let JSAPIPARAMS = null

function jsApiCall(fn) {
  WeixinJSBridge.invoke('getBrandWCPayRequest', JSAPIPARAMS, function(res) {
    console.log('--------------')
    console.log(res)
    fn && fn(res)
    WeixinJSBridge.log(res.err_msg)
  })
}

export default {
  props: {
    activityId: null
  },
  data() {
    return {
      activityInfo: null,
      personCount: 1,
      petCount: 1,
      status: 1,
      userInfo: null,
      orderInfo: null
    }
  },
  computed: {
    petUnitPrice() {
      if (!this.activityInfo) {
        return
      }

      const p = this.activityInfo.petUnitPrice

      return `${p}/`
    },
    personUnitPrice() {
      if (!this.activityInfo) {
        return
      }

      const p = this.activityInfo.personUnitPrice

      return `${p}/`
    },
    allPrice() {
      if (!this.activityInfo) {
        return 0
      }

      const personPrice = this.personCount * this.activityInfo.personUnitPrice
      const petPrice = this.petCount * this.activityInfo.petUnitPrice

      return Math.floor(personPrice) + Math.floor(petPrice)
    }
  },
  methods: {
    toast(msg) {
      const toast = this.$f7.toast.create({
        text: msg,
        closeTimeout: 2000,
        position: 'center'
      })
      toast.open()
    },
    async submitData() {
      try {
        if (!this.activityInfo || !this.userInfo) {
          return
        }

        if (this.activityInfo.tag && this.activityInfo.tag.length) {
          let key
          const flag = this.activityInfo.tag.some(k => {
            if (!this.userInfo[k]) {
              key = k
              return true
            }

            return false
          })

          if (flag) {
            return this.$f7.dialog.confirm('', null, () => {
              this.$f7router.navigate('/person/update')
            })
          }
        }

        if (this.status === 2) {
          return this.toast('')
        }

        const { iRet, data } = await joinActivity({
          activityId: this.activityId
        })

        if (iRet === 0) {
          this.status = 2
          setTimeout(() => {
            this.back()
          }, 1000)
        } else {
          this.toast('')
        }
      } catch (error) {
        console.error(error)
      }
    },

    async orderCreate() {
      try {
        if (!this.activityInfo) {
          return
        }

        if (!this.orderInfo) {
          const data = await createOrder({
            petCount: this.petCount,
            personCount: this.personCount,
            activityId: this.activityInfo.activityId
          })

          if (data.iRet === 0) {
            this.orderInfo = data.data
          }
        }

        if (this.orderInfo.orderId) {
          location.href = `/order/j-pay/?orderId=${this.orderInfo.orderId}`
        }

        // const jsApiParams = await unifiedOrder({
        //   orderId: this.orderInfo.orderId
        // })
        // JSAPIPARAMS = jsApiParams.data

        // if (jsApiParams.iRet === 0) {
        //   this.callpay(res => {})
        // } else {
        //   console.log(jsApiParams)
        //   this.toast('')
        // }
      } catch (error) {
        console.error(error)
      }
    },

    callpay(fn) {
      if (typeof WeixinJSBridge == 'undefined') {
        if (document.addEventListener) {
          document.addEventListener(
            'WeixinJSBridgeReady',
            jsApiCall.bind(null, [fn]),
            false
          )
        } else if (document.attachEvent) {
          document.attachEvent(
            'WeixinJSBridgeReady',
            jsApiCall.bind(null, [fn])
          )
          document.attachEvent(
            'onWeixinJSBridgeReady',
            jsApiCall.bind(null, [fn])
          )
        }
      } else {
        jsApiCall(fn)
      }
    },

    back() {
      this.$f7router.back()
    },

    onPersonChange(value) {
      this.personCount = value
    },
    onPetChange(value) {
      this.petCount = value
    },
    onPageBeforeIn() {
      this.getActivityInfo()
      this.getUserInfo()
      this.getOrderInfo()
    },
    async getOrderInfo() {
      try {
        const { iRet, data } = await getOrderInfoForActivityId({
          activityId: this.activityId
        })

        if (iRet === 0 && data) {
          this.orderInfo = data
        }
      } catch (error) {
        console.log(error)
      }
    },
    async getUserInfo() {
      try {
        const { iRet, data } = await getUserInfo()
        if (iRet === 0) {
          this.userInfo = data
        } else {
          console.error(data)
        }
      } catch (error) {
        console.error(error)
      }
    },
    async getActivityInfo() {
      try {
        if (!this.activityId) {
          return
        }

        const { iRet, data } = await getActivityDetail({
          activityId: this.activityId
        })

        if (iRet === 0) {
          this.activityInfo = data
          this.status = data.status
        } else {
          console.error(data, iRet)
        }
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

