<template>
  <f7-page name="personDetail" no-toolbar @page:beforein="onPageBeforeIn">
    <f7-navbar :back-link="backText" sliding title=""></f7-navbar>

    <f7-block-title></f7-block-title>
    <f7-list class="me-person-detail-list media-list" v-if="userInfo">
      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.name || ''}}</div>
          </div>
        </a>
      </li>
      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.nickname || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.mobile || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.age || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{gender || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.city || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.province || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.address || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{userInfo.idCard || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header">/cm</div>
            </div>
            <div class="item-text">{{userInfo.high || ''}}</div>
          </div>
        </a>
      </li>
    </f7-list>

    <f7-block v-if="userInfo">
      <f7-row>
        <f7-button href="/person/update" class="col" fill raised color="green"></f7-button>
      </f7-row>
    </f7-block>

    <f7-block v-else inset></f7-block>
  </f7-page>
</template>



<script>
import { getUserInfo } from '../utils'

export default {
  data() {
    return {
      backText: '',
      userInfo: null
    }
  },
  computed: {
    gender() {
      if (this.userInfo.gender == 1) {
        return ''
      } else if (this.userInfo.gender === 0) {
        return ''
      } else {
        return ''
      }
    }
  },
  methods: {
    async onPageBeforeIn() {
      try {
        const { iRet, data } = await getUserInfo()

        if (iRet === 0) {
          this.userInfo = data
        } else {
          console.error(data)
        }
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style scoped>
.me-person-detail-list {
  word-break: break-all;
}
.me-list-item .item-title,
.me-list-item .item-text {
  color: black;
  font-weight: inherit;
  font-size: 17px;
}

.me-list-item .item-title > .item-header {
  color: grey;
  font-size: 16px;
  padding-bottom: 5px;
}
</style>
