<template>
  <f7-page name="petDetail" no-toolbar @page:beforein="onPageBeforeIn">
    <f7-navbar :back-link="backText" sliding title=""></f7-navbar>

    <f7-list class="me-pet-detail-list media-list" v-if="petInfo">
      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{petInfo.nickname || ''}}</div>
          </div>
        </a>
      </li>
      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{gender || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{petInfo.age || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{vaccineStatus || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{petInfo.petType || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{petInfo.subType || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{petInfo.weight || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{neuter}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{this.petInfo.size || ''}}</div>
          </div>
        </a>
      </li>

      <li class="me-list-item">
        <a class="item-content">
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header"></div>
            </div>
            <div class="item-text">{{this.petInfo.color || ''}}</div>
          </div>
        </a>
      </li>
    </f7-list>

    <f7-block v-if="petInfo">
      <f7-row>
        <f7-button :href="link" class="col" fill raised color="green"></f7-button>
      </f7-row>
    </f7-block>

    <f7-block v-else inset></f7-block>
  </f7-page>
</template>

<style scoped>
.me-list-item .item-title,
.me-list-item .item-text {
  color: black;
  font-weight: inherit;
  font-size: 17px;
}

.me-list-item .item-title > .item-header {
  color: grey;
  font-size: 16px;
  padding-bottom: 5px;
}
</style>


<script>
import { getPetDetail } from '../utils'

export default {
  props: {
    petId: null
  },
  data() {
    return {
      backText: '',
      petInfo: null
    }
  },
  computed: {
    link() {
      if (this.petId) {
        return `/pet/update/${this.petId}`
      } else {
        return '#'
      }
    },
    vaccineStatus() {
      if (this.petInfo.vaccineStatus == 1) {
        return ''
      } else {
        return ''
      }
    },
    gender() {
      if (this.petInfo.gender == 1) {
        return ''
      } else {
        return ''
      }
    },
    neuter() {
      if (this.petInfo.neuter == 1) {
        return ''
      } else {
        return ''
      }
    }
  },
  methods: {
    onPageBeforeIn() {
      this.getPetDetail()
    },
    async getPetDetail() {
      try {
        const data = await getPetDetail({ petId: this.petId })

        if (data.iRet === 0) {
          this.petInfo = data.data
        } else {
          console.error(data)
        }
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>
